.zinc {
  padding: 5% 12%;
  background: url("../../assets/images/zinc_bg.jpg") no-repeat fixed center;
  background-size: cover;
  color: #FFFFFF; }
  .zinc__container {
    width: 100%;
    margin: auto;
    margin-top: 10px; }

@media (min-width: 768px) and (max-width: 1024px) {
  .zinc {
    background: url("../../assets/images/zinc_bg_ipad.jpg") no-repeat fixed center; } }

@media (min-width: 768px) and (max-width: 1024px) and (orientation: landscape) {
  .zinc {
    background: url("../../assets/images/zinc_bg_ipad_land.jpg") no-repeat center center fixed; } }

@media (min-width: 481px) and (max-width: 767px) {
  .zinc {
    background: url("../../assets/images/zinc_bg_mob.jpg") no-repeat center center fixed; } }

@media (min-width: 320px) and (max-width: 480px) {
  .zinc {
    padding: 10%;
    background: url("../../assets/images/zinc_bg_mob.jpg") no-repeat center center fixed; }
    .zinc__container {
      width: 100%; } }



