.header {
  height: 100vh; }
  .header__section {
    padding: 0 10%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: start;
        align-items: flex-start;
    -ms-flex-pack: center;
        justify-content: center;
    color: white; }
  .header .parallax {
    z-index: -1;
    height: 100vh;
    background: url("../../assets/images/header_bg.jpg") no-repeat fixed center;
    background-size: cover; }

@media (min-width: 768px) and (max-width: 1024px) {
  .header__section {
    padding: 0 5%; }
  .header .parallax {
    background: url("../../assets/images/header_bg_ipad.jpg") no-repeat center center fixed; } }

@media (min-width: 768px) and (max-width: 1024px) and (orientation: landscape) {
  .header__section {
    padding: 0 5%; }
  .header .parallax {
    background: url("../../assets/images/header_bg_ipad_land.jpg") no-repeat center center fixed; } }

@media (min-width: 481px) and (max-width: 767px) {
  .header__section {
    padding: 0 5%; }
  .header .parallax {
    background: url("../../assets/images/header_bg_mob.jpg") no-repeat center center fixed; } }

@media (min-width: 320px) and (max-width: 480px) {
  .header__section {
    padding: 0 5%; }
  .header .parallax {
    background: url("../../assets/images/header_bg_mob.jpg") no-repeat center center fixed; } }



