.news {
  padding: 5% 12%;
  background: url("../../assets/images/news_bg.jpg") no-repeat fixed center;
  background-size: cover;
  color: #FFFFFF; }
  .news__container {
    width: 100%;
    margin: auto;
    margin-top: 10px; }
    .news__container p {
      padding: 2% 10% 0 10%;
      text-align: left;
      color: #222222; }

@media (min-width: 768px) and (max-width: 1024px) {
  .news {
    background: url("../../assets/images/news_bg_ipad.jpg") no-repeat fixed center; } }

@media (min-width: 768px) and (max-width: 1024px) and (orientation: landscape) {
  .news {
    background: url("../../assets/images/news_bg_ipad_land.jpg") no-repeat center center fixed; } }

@media (min-width: 481px) and (max-width: 767px) {
  .news {
    background: url("../../assets/images/news_bg_mob.jpg") no-repeat center center fixed; } }

@media (min-width: 320px) and (max-width: 480px) {
  .news {
    background: url("../../assets/images/news_bg_mob.jpg") no-repeat center center fixed;
    padding: 10%; }
    .news__container {
      width: 100%; } }



